/**
 * Tiptap Content Utilities
 * 
 * Utilitrios para processamento de contedo Tiptap Editor.
 * Extrao de texto, gerao de excerpts, validao e estatsticas.
 * 
 * @module @rainersoft/utils/content
 * @author Rainer Teixeira
 * @version 1.0.0
 * @since 1.0.0
 */

// ============================================================================
// TYPES
// ============================================================================

export interface TiptapNode {
  type: string;
  text?: string;
  content?: TiptapNode[];
  attrs?: Record<string, any>;
}

export interface TiptapJSON {
  type: 'doc';
  content: TiptapNode[];
}

export interface ContentStats {
  wordCount: number;
  characterCount: number;
  readingTime: number;
}

// ============================================================================
// TEXT EXTRACTION
// ============================================================================

/**
 * Extrai texto de contedo Tiptap (JSON)
 * 
 * Processa recursivamente a estrutura JSON do Tiptap para extrair
 * todo o texto em formato string, ignorando marcaes e metadados.
 * 
 * @param content - Contedo JSON do Tiptap
 * @returns Texto extrado do contedo
 * 
 * @example
 * ```ts
 * const content = {
 *   type: 'doc',
 *   content: [
 *     { type: 'paragraph', content: [{ type: 'text', text: 'Ol mundo' }] }
 *   ]
 * };
 * extractTextFromTiptap(content) // "Ol mundo"
 * ```
 */
export function extractTextFromTiptap(content: TiptapJSON): string {
  /**
   * Extrai texto de um n Tiptap (recursivo)
   */
  function extractFromNode(node: TiptapJSON | TiptapNode): string {
    if ('text' in node && typeof node.text === 'string') {
      return node.text;
    }

    if ('content' in node && Array.isArray(node.content)) {
      return node.content
        .map((child: TiptapNode) => extractFromNode(child))
        .join(' ');
    }

    return '';
  }

  return extractFromNode(content).trim();
}

/**
 * Gera excerpt (resumo) do contedo Tiptap
 * 
 * Extrai texto do contedo e limita a um tamanho mximo.
 * Perfeito para previews, meta descriptions, etc.
 * 
 * @param content - Contedo JSON do Tiptap
 * @param maxLength - Tamanho mximo do excerpt (padro: 160 caracteres)
 * @returns Excerpt formatado com "..." se truncado
 * 
 * @example
 * ```ts
 * const content = { type: 'doc', content: [...] };
 * generateExcerpt(content, 50) // "Primeiros 50 caracteres..."
 * ```
 */
export function generateExcerpt(
  content: TiptapJSON,
  maxLength: number = 160
): string {
  const text = extractTextFromTiptap(content);

  if (text.length <= maxLength) {
    return text;
  }

  return text.substring(0, maxLength).trim() + '...';
}

// ============================================================================
// CONTENT CREATION
// ============================================================================

/**
 * Cria contedo JSON vazio do Tiptap
 * 
 * Retorna uma estrutura Tiptap vlida mas vazia.
 * til para inicializar editores ou criar novos posts.
 * 
 * @returns Documento vazio do Tiptap
 * 
 * @example
 * ```ts
 * const emptyContent = createEmptyTiptapContent();
 * // { type: 'doc', content: [] }
 * ```
 */
export function createEmptyTiptapContent(): TiptapJSON {
  return {
    type: 'doc',
    content: [],
  };
}

// ============================================================================
// CONTENT VALIDATION
// ============================================================================

/**
 * Verifica se o contedo Tiptap est vazio
 * 
 * Analisa o contedo para determinar se no h texto til.
 * Considera espaos em branco e elementos vazios.
 * 
 * @param content - Contedo JSON do Tiptap
 * @returns true se o contedo estiver vazio
 * 
 * @example
 * ```ts
 * const empty = { type: 'doc', content: [] };
 * isContentEmpty(empty) // true
 * 
 * const withText = { type: 'doc', content: [
 *   { type: 'paragraph', content: [{ type: 'text', text: 'Ol' }] }
 * ]};
 * isContentEmpty(withText) // false
 * ```
 */
export function isContentEmpty(content: TiptapJSON): boolean {
  if (!content || !content.content || content.content.length === 0) {
    return true;
  }

  const text = extractTextFromTiptap(content);
  return text.trim().length === 0;
}

// ============================================================================
// CONTENT STATISTICS
// ============================================================================

/**
 * Conta palavras no contedo Tiptap
 * 
 * Conta o nmero de palavras usando um algoritmo simples
 * que separa por espaos em branco.
 * 
 * @param content - Contedo JSON do Tiptap
 * @returns Nmero de palavras no contedo
 * 
 * @example
 * ```ts
 * const content = { type: 'doc', content: [
 *   { type: 'paragraph', content: [{ type: 'text', text: 'Ol mundo' }] }
 * ]};
 * countWords(content) // 2
 * ```
 */
export function countWords(content: TiptapJSON): number {
  const text = extractTextFromTiptap(content);
  return text.trim().split(/\s+/).filter(word => word.length > 0).length;
}

/**
 * Conta caracteres no contedo Tiptap
 * 
 * @param content - Contedo JSON do Tiptap
 * @returns Nmero de caracteres no contedo
 */
export function countCharacters(content: TiptapJSON): number {
  return extractTextFromTiptap(content).length;
}

/**
 * Calcula tempo de leitura estimado
 * 
 * Baseado na velocidade mdia de leitura de 200 palavras por minuto.
 * 
 * @param content - Contedo JSON do Tiptap
 * @param wordsPerMinute - Velocidade de leitura (padro: 200)
 * @returns Tempo de leitura em minutos
 * 
 * @example
 * ```ts
 * const content = { type: 'doc', content: [...] };
 * getReadingTime(content) // 2.5 (para 500 palavras)
 * ```
 */
export function getReadingTime(
  content: TiptapJSON,
  wordsPerMinute: number = 200
): number {
  const wordCount = countWords(content);
  return Math.ceil(wordCount / wordsPerMinute);
}

/**
 * Obtm estatsticas completas do contedo
 * 
 * Retorna um objeto com todas as mtricas relevantes:
 * - Contagem de palavras
 * - Contagem de caracteres
 * - Tempo de leitura estimado
 * 
 * @param content - Contedo JSON do Tiptap
 * @returns Estatsticas completas do contedo
 * 
 * @example
 * ```ts
 * const stats = getContentStats(content);
 * console.log(stats);
 * // { wordCount: 150, characterCount: 800, readingTime: 1 }
 * ```
 */
export function getContentStats(content: TiptapJSON): ContentStats {
  const wordCount = countWords(content);
  const characterCount = extractTextFromTiptap(content).length;
  const readingTime = getReadingTime(content);

  return {
    wordCount,
    characterCount,
    readingTime,
  };
}

// ============================================================================
// CONTENT TRANSFORMATION
// ============================================================================

/**
 * Limpa e normaliza texto do contedo
 * 
 * Remove espaos extras, normaliza quebras de linha
 * e limpa caracteres especiais indesejados.
 * 
 * @param content - Contedo JSON do Tiptap
 * @returns Texto limpo e normalizado
 */
export function cleanText(content: TiptapJSON): string {
  const text = extractTextFromTiptap(content);
  
  return text
    .replace(/\s+/g, ' ') // Normaliza espaos
    .replace(/\n\s*\n/g, '\n') // Remove quebras de linha vazias
    .replace(/^\s+|\s+$/g, '') // Remove espaos no incio/fim
    .trim();
}

/**
 * Verifica se o contedo contm texto especfico
 * 
 * Busca case-insensitive por uma palavra ou frase no contedo.
 * 
 * @param content - Contedo JSON do Tiptap
 * @param searchText - Texto para buscar
 * @returns true se encontrar o texto
 * 
 * @example
 * ```ts
 * const content = { type: 'doc', content: [
 *   { type: 'paragraph', content: [{ type: 'text', text: 'Ol mundo' }] }
 * ]};
 * containsText(content, 'mundo') // true
 * containsText(content, 'planeta') // false
 * ```
 */
export function containsText(content: TiptapJSON, searchText: string): boolean {
  const text = extractTextFromTiptap(content).toLowerCase();
  return text.includes(searchText.toLowerCase());
}

/**
 * Substitui texto no contedo Tiptap
 * 
 * Busca e substitui texto em todo o contedo.
 * Operao case-insensitive.
 * 
 * @param content - Contedo JSON do Tiptap
 * @param searchText - Texto para buscar
 * @param replaceText - Texto para substituir
 * @returns Contedo atualizado com o texto substitudo
 * 
 * @example
 * ```ts
 * const content = { type: 'doc', content: [
 *   { type: 'paragraph', content: [{ type: 'text', text: 'Ol mundo' }] }
 * ]};
 * const updated = replaceText(content, 'mundo', 'planeta');
 * // Resultado: "Ol planeta"
 * ```
 */
export function replaceText(
  content: TiptapJSON,
  searchText: string,
  replaceText: string
): TiptapJSON {
  /**
   * Processa n recursivamente para substituir texto
   */
  function processNode(node: TiptapNode): TiptapNode {
    const newNode = { ...node };

    // Substituir texto no n atual
    if ('text' in node && typeof node.text === 'string') {
      newNode.text = node.text.replace(
        new RegExp(searchText, 'gi'),
        replaceText
      );
    }

    // Processar ns filhos recursivamente
    if ('content' in node && Array.isArray(node.content)) {
      newNode.content = node.content.map(child => processNode(child));
    }

    return newNode;
  }

  // Processar contedo principal
  const newContent = {
    ...content,
    content: content.content.map(node => processNode(node))
  };

  return newContent;
}
